<template>
	<div class="full-height">
		<div class="row no-wrap">
			<q-toolbar class="paper" style="min-height: 56px">
				<q-toolbar-title shrink>{{ $t('Styles') }}</q-toolbar-title>
			</q-toolbar>
		</div>
		
		<paper :style="{ 'height': (pageHeight-66-16) + 'px' }">
			<inputs
				type="textarea"
				label="CSS Editor"
				@onChange="onChange"
				:value="getValue"
				style="height: 100%"
				full-height
				/>
			
		</paper>
	</div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'

export default defineComponent({
	'name': 'StylesPage',
	
	'methods': {
		onChange({ id, value }) {
			this.$store.commit('editor/set', { 'styles': value });
		}
	},
	
	'computed': {
		pageHeight() {
			return this.$store.state.jarvis.pageHeight;
		},
		getValue() {
			return this.$store.state.editor.styles;
		}
	}
});
</script>
